        articleImg16: "",

        subtitle17: "",
        mediumText17: "",
        paragraph17:
            "The other important coal-producing Indian states are Madhya Pradesh, Andhra Pradesh, and Maharashtra. Most of the nation's lignite reserves are located in the state of Tamil Nadu in the south.",
