        articleImg16: "",

        subtitle17: "",
        mediumText17: "",
        paragraph17:
            "The country's energy sector is still pushing for a capacity goal in the upcoming five-year plan that would allow for the construction of up to 200 additional coal-fired generating units by 2025. ",
